import React from "react"
import { Redirect } from '@reach/router'
import Img from 'gatsby-image'
import Logo from "../components/logo"
import styled from "styled-components"
import posed from "react-pose"

import SEO from "../components/seo"

const Information = styled.div`
  background-color: ${props => props.color};
  font-family: 'Calibre-Regular';
  color: white;
  height: 100%;
  position: relative;
  z-index: 990;

  @media(min-width: 992px) {
    height: 100%;
  }
`
const Title = styled.h1`
  font-size: 48px;
  margin-left: 25px;
  margin-right: 25px;
  padding-top: 25px;
`

const Text = styled.p`
  font-size: 24px;
  margin-left: 25px;
  margin-right: 25px;
  padding-top: 25px;
  max-width: 800px;
`

const Transition = posed.div({
  enter: {
    opacity: 1,
    transition: {
      duration: 600,
      ease: 'easeInOut'
      }
  },
    exit: { 
      opacity: 0,
    transition: {
      duration: 600,
      ease: 'easeInOut'
      },
      
  },
})


class IndexPage extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      fetchedPages: null
    }
  }

  componentWillMount(){
    this.setState({
      fetchedPages: this.props.data.allPrismicPage.edges
    })

  }

  render(){

    let {fetchedPages} = this.state;

    return(
  
      <Transition>
      <Redirect noThrow to="/" />
      <SEO title="Home" keywords={[`gatsby`, `application`, `react`]} />
      {
        fetchedPages.map((item) => {
          return(
            <>
              {item.node.data.image_gallery.map((image, index) => {
                return <Img key={index} fluid={image.image.localFile.childImageSharp.fluid} />
                // <img key={index} src={image.image.url} />
              }
            )}
            <Information color={`${item.node.data.color}`}>
              <Title>{item.node.data.title.text}</Title>
              <Text>{item.node.data.text.text}</Text>
            </Information>
            </>
          )
        })
      }
        <Logo/>
      </Transition>
    )
  }
}

export default IndexPage

export const query = graphql`
{
  allPrismicPage {
    edges {
      node {
        id
        uid
        data {
          title {
            text
          }
          text {
            text
          }
          color
          image_gallery {
            image {
              alt
              url
              localFile {
                childImageSharp {
                  fluid(maxWidth: 1500) {
                    ...GatsbyImageSharpFluid_withWebp
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
`
